.item {
  position: relative;
  display: flex;
  /* justify-content: space-between; */
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 14px;
  color: #000;
  width: 100%;
}

.item:last-of-type {
  
}

.item:not(:last-child) {
  margin-bottom: 32px;
}

.item:after {
  content: '';
  position: absolute;
  max-width: 610px;
  width: 100%;
  height: 1px;
  border-bottom: 1px solid #e5e5e5;
  left: 0;
  bottom: -16px;
}

.users {
  width: 100%;
}

.buttonsContainer {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 15px;
}

.container {
  max-width: 610px;
  width: 100%;
  /* display: flex; */
}

.thead {
  display: flex;
  align-items: center;
  background-color: #e5e5e5;
  width: 100%;
  height: 36px;
  margin-bottom: 15px;
}

.thead > :first-child {
  display: flex;
  width: 100%;
}

.thead > :first-child > td {
  flex: 0 1 50%;
  align-self: baseline;
}

.nameCol {
  font-weight: 500;
}

.ageCol {
  font-weight: 500;
}

tbody > tr > td {
  flex: 0 1 50%;
}

tr > :last-child {
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 14px;
}

tr > :first-child {
  padding-left: 15px;
}