.button {
  padding: 7px 24px;
  border-radius: 5px;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #fff;
  border: none;
  cursor: pointer;
}

.button:not(:last-child) {
  margin-right: 25px;
}

.disabled {

}

.secondary {
  background-color: transparent;
  color: #06c;
  border: 1px solid #06c;
}

.default {
  background-color: #0066CC;
}

.red {
  background: #cc1439;
}

.default:hover {
  background: #0080ff;
}

.red:hover {
  background: #ff1a47;
}

.secondary:hover {
  background: #e5f0fa;
}


.default:active {
  background: #0059b2;
}

.secondary:active {
  background: #c9e5ff;
}

.red:active {
  background: #b21232;
}

.button:disabled {
  color: #002e5c;
  background: #004d99;
  opacity: 0.5;
}
