.sidebar {
  display: none;
}

.sidebar.open {
  position: absolute;
  max-width: 317px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 10px 40px 0 rgba(29, 33, 38, 0.13);
  background: #fff;
  z-index: 50;
}

.background {
  position: absolute;
  height: 100vh;
  top: 0;
  z-index: 10;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000;
  opacity: 0.3;
}

.nav {
  display: flex;
  flex-direction: column;
  margin-left: 70px;
}

.nav a {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 22px;
  color: #000;
  text-decoration: none;
  transition: color 0.2s ease-out;
}

.nav a:not(:last-child) {
  margin-bottom: 45px;
}

.nav a:hover {
  color: #58acff;
}

.nav a.active {
  text-decoration: underline;
  text-decoration-skip-ink: none;
  color: #0059b2;
}

.close {
  border: none;
  background-color: transparent;
  cursor: pointer;
  margin: 16px 24px 25px auto;
}

.close img {
  cursor: pointer;
}
